* {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}


img {
  width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

picture {
  display: block;
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 0;
}

svg {
  fill: currentColor;
  width: 100%;
}

.none {
  display: none;
}

.visualy-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.start-sb {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}

.start-sa {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: flex-start;
}

.center-sb {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

